.monthHeadList {
   list-style: none;

   background-color: var(--calendar); // #22232D; dark
   color: #616161; // #ffffff; dark

   display: flex;
   justify-content: space-between;

   font-weight: 600;
   font-size: 16px;
   line-height: 1.125;
   text-transform: uppercase;

   padding: 16px;
   margin-bottom: 14px;

   border: 1px solid rgba(220, 227, 229, 0.5); // 1px solid rgba(255, 255, 255, 0.15); dark
   border-radius: 8px;

   @media screen and (min-width: 768px) {
      // background-color: #21222C; dark

      padding: 14px 40px;
      margin-bottom: 18px;

      border: 1px solid rgba(220, 227, 229, 0.5); // 1px solid rgba(255, 255, 255, 0.15); dark
   }

   @media screen and (min-width: 1440px) {
      padding: 14px 60px;
      margin-bottom: 15px;
   }
}

.monthHeadItem {
   color: var(--notheme-color-accent);
}
